.header__root {
  padding-top: 8px;
}

.header__container {
  display: flex;
  align-items: center;
  height: 50px;
  font-size: 30px;
}

@media only screen and (max-width: 700px) {
  .header__container {
    font-size: 24px;
  }
}

.header__link {
  padding: 0;
  line-height: 50px;
  color: inherit;
  text-decoration: none;
  white-space: nowrap;
}

